import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import axios from "axios";

const API_URL = process.env.REACT_APP_API_URL
  ? process.env.REACT_APP_API_URL
  : "";

const initialState = {
  skillsData: localStorage.getItem("skillsData")
    ? JSON.parse(localStorage.getItem("skillsData"))
    : {},
};

export const getSkillQnA = createAsyncThunk(
  "getSkillQnA",
  async (req, body) => {
    try {
      const config = {
        headers: {
          "Content-type": "application/json",
        },
      };
      const { data } = await axios.get(
        `${API_URL}/api/skills/getQnA/${req.skillId}`,
        body,
        config
      );
      return await data;
    } catch (error) {
      console.log(error);
    }
  }
);

export const createSkillQnAData = createAsyncThunk(
  "createuserskills",
  async (body) => {
    try {
      const config = {
        headers: {
          "Content-type": "application/json",
        },
      };
      if (body.id) {
        const { data } = await axios.post(
          `${API_URL}/api/skills/updateQnA`,
          body,
          config
        );
        return await data;
      } else {
        const { data } = await axios.post(
          `${API_URL}/api/skills/createQnA`,
          body,
          config
        );
        return await data;
      }
    } catch (error) {
      console.log(error);
    }
  }
);

