export const deleteUserSkillData = createAsyncThunk(
  "deleteuserskill",
  async (body) => {
    try {
      const config = {
        headers: {
          "Content-type": "application/json",
        },
      };
      const { data } = await axios.post(
        `${API_URL}/api/skills/delete`,
        body,
        config
      );
      return await data;
    } catch (error) {
      console.log(error);
    }
  }
);

export const skillReducer = createSlice({
  name: "userSkills",
  initialState,
  reducers: {},
  extraReducers: {
    [getUserSkillsData.fulfilled]: (state, action) => {
      state.homeScreenLoading = false;
      if (action.payload.error) {
        state.homeScreenError = action.payload.error;
        state.homeScreenSuccess = "";
      } else {
        state.userSkill = action.payload;
        state.success = "";
      }
    },
    [getUserSkillsData.pending]: (state, action) => {
      state.homeScreenLoading = true;
      state.userSkill = null;
      state.homeScreenSuccess = "";
      state.homeScreenError = "";
    },
    [getUserSkillsData.rejected]: (state, action) => {
      state.homeScreenLoading = false;
      if (action.payload.error) {
        state.userSkill = null;
        state.homeScreenError = action.payload.error;
      }
    },
    [createUserSkillsData.fulfilled]: (state, action) => {
      state.homeScreenLoading = false;
      if (action.payload.error) {
        state.homeScreenError = action.payload.error;
        state.homeScreenSuccess = "";
      } else {
        state.userSkill = action.payload;
        state.success = "";
      }
    },
    [createUserSkillsData.pending]: (state, action) => {
      state.homeScreenLoading = true;
      state.homeScreenSuccess = "";
      state.homeScreenError = "";
    },
    [createUserSkillsData.rejected]: (state, action) => {
      state.homeScreenLoading = false;
      if (action.payload.error) {
        state.homeScreenError = action.payload.error;
      }
    },
    [deleteUserSkillData.fulfilled]: (state, action) => {
      state.homeScreenLoading = false;
      if (action.payload.error) {
        state.homeScreenError = action.payload.error;
        state.homeScreenSuccess = "";
      } else {
        state.userSkill = action.payload;
        state.success = "";
      }
    },
    [deleteUserSkillData.pending]: (state, action) => {
      state.homeScreenLoading = true;
      state.homeScreenSuccess = "";
      state.homeScreenError = "";
    },
    [deleteUserSkillData.rejected]: (state, action) => {
      state.homeScreenLoading = false;
      if (action.payload.error) {
        state.homeScreenError = action.payload.error;
      }
    },
  },
});

export default skillReducer.reducer;
