import React, { useEffect, useState } from "react";
import Base from "../Base";
import { Button, Col, Row } from "reactstrap";
import { useDispatch, useSelector } from "react-redux";
import {
  createSkillQnAData,
  deleteSkillQnAData,
  getSkillInfo,
  getSkillQnA,
} from "../reducers/skillReducer";
import { useParams } from "react-router-dom";

import AddSkillsQnAComponentModal from "../shared-components/AddSkillsQnAComponentModal";
import ProfileSkillQnASection from "../shared-components/ProfileSkillQnASection";
import AnimatedElement from "../shared-components/AnimatedElement";

const SkillsQnA = () => {
  // const skillsQnAList = [
  //   {
  //     _id: 1,
  //     skillId: 1,
  //     active: true,
  //     question: "Question 1",
  //     userId: 122,
  //     answer:
  //       "An analytical senior-level software developer with deep expertise in UI/UX design and Frontend development posses 12+ year hands on experience. Versed in agile development techniques. Skilled in requirements analysis and project documentation. Expertise in current and emerging trends and techniques. Proficient in design, coding and developing the new program",
  //     date: "20/02/2022",
  //   },
  //   {
  //     _id: 2,
  //     active: true,
  //     skillId: 1,
  //     question: "Question 2",
  //     userId: 122,
  //     answer: "Answer entered",
  //     date: "20/02/2022",
  //   },
  //   {
  //     _id: 3,
  //     active: true,
  //     skillId: 1,
  //     question: "Question 3",
  //     userId: 122,
  //     answer: "Answer entered",
  //     date: "20/02/2022",
  //   },
  //   {
  //     _id: 4,
  //     active: true,
  //     skillId: 1,
  //     question: "Question 4",
  //     userId: 122,
  //     answer: "Answer entered",
  //     date: "20/02/2022",
  //   },
  // ];

  const { skillId } = useParams();

  const [skillData, setSkillData] = useState(null);
  const [skillQnAList, setSkillQnAList] = useState(null);

  const { userdetails } = useSelector((state) => state.user);
  const [addQnAsModal, setAddQnAModal] = useState(false);
  const [updateQnA, setUpdateQnA] = useState(true);
  const formFieldTypes = ["question-text-require", "answer-quill-optional"];
  const [formFieldValues, setFormFieldValues] = useState({});
  const dispatch = useDispatch();

  const getSkillQnAList = async (skillId) => {
    const data = await dispatch(getSkillQnA({ skillId }));
    if (!data.payload.error) {
      await setSkillQnAList(data.payload);
    } else {
      await setSkillQnAList(null);
    }
  };

  const updateStatus = async (id, active) => {
    const data = await dispatch(
      createSkillQnAData({
        id,
        active,
        token: userdetails.token,
      })
    );
    if (data) {
      let tempSkills = [...skillQnAList];
      await skillQnAList.forEach((item, i) => {
        if (item._id === data.payload._id) {
          tempSkills[i] = data.payload;
        }
      });
      setSkillQnAList(tempSkills);
    }
  };

  const closeModal = () => {
    setAddQnAModal(false);
    setUpdateQnA(true);
    setFormFieldValues({});
  };
  const openModal = () => {
    setAddQnAModal(true);
    setUpdateQnA(false);
  };

  const editQnA = async (question, answer, id) => {
    await setFormFieldValues({
      ...formFieldValues,
      question,
      answer,
      id,
    });
    setAddQnAModal(true);
    setUpdateQnA(false);
  };

  const getSkill = async (skillId) => {
    const data = await dispatch(getSkillInfo({ _id: skillId }));
    if (!data.payload.error) {
      await setSkillData(data.payload);
      await getSkillQnAList(skillId);
    } else {
      await setSkillData(null);
    }
  };

  const deleteSkills = async (id) => {
    await dispatch(deleteSkillQnAData({ id: id, token: userdetails.token }));
    getSkill(skillId);
  };

  useEffect(() => {
    getSkill(skillId);
  }, [userdetails, updateQnA]);

  return (
    <Base>
      {skillData && (
        <div className="container-fluid p-md-5 profile">
          <h4 className="p-color pt-3">{skillData.skillName}:</h4>
          <div className="t-back2 p-3">
            <Row style={{ position: "sticky", top: "60px", zIndex: 9 }}>
              <Col className="d-flex justify-content-end">
                <Button
                  className="sp-btn-secondary rounded-0 px-5 mb-4 mb-md-0"
                  onClick={openModal}
                >
                  Add New Question
                </Button>
              </Col>
            </Row>
            {skillQnAList &&
              skillQnAList.map((item, i) => (
                <AnimatedElement key={item._id}>
                  <ProfileSkillQnASection
                    item={item}
                    updateStatus={updateStatus}
                    editQnA={editQnA}
                    deleteSkills={deleteSkills}
                    index={i + 1}
                  />
                </AnimatedElement>
              ))}
          </div>
        </div>
      )}
      {addQnAsModal && (
        <AddSkillsQnAComponentModal
          setAddQnAModal={closeModal}
          formField={formFieldTypes}
          formInputValues={formFieldValues}
          skillId={skillId}
        />
      )}
    </Base>
  );
};

export default SkillsQnA;
