import React, { Fragment, useEffect, useState } from "react";
import {
  Button,
  Col,
  Form,
  FormGroup,
  Input,
  Label,
  Modal,
  ModalBody,
  ModalHeader,
  Row,
} from "reactstrap";
import { LoaderSM } from "./loader";
import EditorComponent from "./EditorComponent";
import { createSkillQnAData } from "../reducers/skillReducer";
import { useDispatch, useSelector } from "react-redux";

const AddSkillsQnAComponentModal = (props) => {
  const { userdetails } = useSelector((state) => state.user);
  const dispatch = useDispatch();
  const addSkillsModal = true;
  const formField = props.formField;
  const formInputValues = props.formInputValues;
  const [formData, setFormData] = useState({ skillId: props.skillId });
  const { loading } = {};
  const args = {
    backdrop: false,
    centered: true,
    size: "lg",
    fade: false,
  };
  useEffect(() => {
    const assignForm = async () => {
      let preformData = await formField.reduce((obj, key, index) => {
        obj[key.split("-")[0]] = formInputValues[key.split("-")[0]];
        return obj;
      }, {});

      setFormData({ ...formData, ...preformData });
    };
    assignForm();
  }, [formInputValues]);

  const submitHandler = async (e) => {
    e.preventDefault();

    const data = await dispatch(
      createSkillQnAData({
        ...formData,
        token: userdetails.token,
        id: formInputValues.id ? formInputValues.id : null,
      })
    );
    if (data.payload) {
      props.setAddQnAModal(false);
    }
  };

  const description = (value, fieldname) => {
    formData[fieldname] = value;
  };
  return (
    <Row>
      <Col>
        <Modal
          isOpen={addSkillsModal}
          style={{ pointerEvents: "none" }}
          {...args}
        >
          <ModalHeader>
            {formInputValues.id
              ? "Update Question and Answer"
              : "Add New Question"}
          </ModalHeader>
          <ModalBody>
            <Form>
              {formField.map((item, i) => {
                var itemData = item.split("-");
                if (itemData[1] === "text") {
                  return (
                    <FormGroup key={itemData[0]} className="px-3">
                      <Label
                        className="text-capitalize s-color"
                        for={itemData[0]}
                      >
                        {itemData[0].split(/(?=[A-Z])/).join(" ")}
                      </Label>
                      <Input
                        type="text"
                        id={itemData[0]}
                        placeholder={itemData[0].split(/(?=[A-Z])/).join(" ")}
                        required={itemData[2] === "require" ? true : false}
                        defaultValue={
                          formData[itemData[0]] ? formData[itemData[0]] : ""
                        }
                        onChange={(e) =>
                          (formData[e.target.id] = e.target.value)
                        }
                      />
                    </FormGroup>
                  );
                }
                if (itemData[1] === "quill" && formData[itemData[0]]) {
                  return (
                    <Fragment key={itemData[0]}>
                      <EditorComponent
                        description={description}
                        descriptionData={formData[itemData[0]]}
                        fieldname={itemData[0]}
                      />
                    </Fragment>
                  );
                }
                if (itemData[1] === "quill" && !formInputValues.id) {
                  return (
                    <Fragment key={itemData[0]}>
                      <EditorComponent
                        description={description}
                        descriptionData={""}
                        fieldname={itemData[0]}
                      />
                    </Fragment>
                  );
                }
                return null;
              })}

              <div className="d-grid py-2">
                <Row className="px-2">
                  <Col className="text-end">
                    <Button
                      className="sp-btn-secondary rounded-0 px-4 px-md-5 mx-2"
                      onClick={(e) => props.setAddQnAModal(false)}
                    >
                      Cancel
                    </Button>
                    <Button
                      type="submit"
                      className="sp-btn-secondary rounded-0 px-4 px-md-5 mx-2"
                      onClick={submitHandler}
                    >
                      {formInputValues.id ? "Update" : "Add Question"}
                    </Button>
                  </Col>
                </Row>
              </div>
            </Form>
          </ModalBody>
          {loading && <LoaderSM />}
        </Modal>
      </Col>
    </Row>
  );
};

export default AddSkillsQnAComponentModal;
