import {
  faEdit,
  faTrash,
  faEllipsisV,
  faEye,
  faEyeSlash,
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import React, { useState } from "react";
import { FormGroup, Input } from "reactstrap";
import parse from "html-react-parser";

const ProfileSkillQnASection = (props) => {
  const [showAnswer, setShowAnswer] = useState(false);
  const [showAction, setShowAction] = useState(false);
  return (
    <div className="border-line-light" key={props.item._id}>
      <h3 className="f-size-15 position-relative">
        <span
          className="p-color cursor-pointer"
          onClick={(e) => setShowAnswer(!showAnswer)}
        >
          <span className="pe-3">{props.index}.</span> {props.item.question}
        </span>

        <div
          className="edit-delete t-color align-item-center d-flex flex-column flex-md-row "
          style={{ right: "0px", top: "0px" }}
        >
          <FontAwesomeIcon
            icon={faEllipsisV}
            onClick={(e) => setShowAction(true)}
            className="mt-1"
            style={{ padding: "0px 7px" }}
          />
        </div>
        {showAction && (
          <>
            <div
              className="blank-background"
              onClick={(e) => setShowAction(false)}
            ></div>
            <div
              className="edit-delete t-color align-item-center d-flex"
              style={{ right: "0px", top: "0px" }}
            >
              <FormGroup switch className="align-self-center">
                <Input
                  role="switch"
                  type="switch"
                  checked={props.item.active}
                  onChange={(e) => {
                    props.updateStatus(props.item._id, !props.item.active);
                    setShowAction(false);
                  }}
                />
                <span className="d-none d-md-inline">
                  {props.item.active ? "Active" : "Inactive"}
                </span>
              </FormGroup>
              <FontAwesomeIcon
                icon={faEdit}
                onClick={(e) => {
                  props.editQnA(
                    props.item.question,
                    props.item.answer,
                    props.item._id
                  );
                  setShowAction(false);
                }}
                className="mt-1"
                style={{ padding: "0px 7px" }}
              />
              <FontAwesomeIcon
                icon={faTrash}
                onClick={(e) => {
                  props.deleteSkills(props.item._id);
                  setShowAction(false);
                }}
                className="mt-1"
                style={{ padding: "0px 7px" }}
              />
            </div>
          </>
        )}
      </h3>
      {showAnswer ? (
        <div className="f-size-15 t-color ql-content">
          {parse(`${props.item.answer}`)}
          <div
            className="p-color f-size-15 f-bold cursor-pointer d-inline ps-3"
            onClick={(e) => setShowAnswer(false)}
          >
            <FontAwesomeIcon
              icon={faEyeSlash}
              className="mt-1"
              style={{ padding: "0px 7px" }}
            />
            Hide Answer
          </div>
        </div>
      ) : (
        <div
          className="p-color f-size-15 f-bold cursor-pointer d-inline ps-3"
          onClick={(e) => setShowAnswer(true)}
        >
          <FontAwesomeIcon
            icon={faEye}
            className="mt-1"
            style={{ padding: "0px 7px" }}
          />
          Show Answer
        </div>
      )}
    </div>
  );
};

export default ProfileSkillQnASection;
