import React, { useState } from "react";
import ReactQuill from "react-quill";
import EditorToolbar, { modules, formats } from "../utils/QuillToolbar";
import "react-quill/dist/quill.snow.css";

function EditorComponent(props) {
  const [descriptionData, setDescriptionData] = useState(props.descriptionData);
  const fieldname = props.fieldname;
  const ondescription = (value) => {
    setDescriptionData(value);
    props.description(value, fieldname);
  };

  //   return <ReactQuill theme="snow" value={value} onChange={setValue} />;
  return (
    <div className="col-md-12 editor px-3">
      {props.title ? (
        <label className="font-weight-bold t-color">{props.title}</label>
      ) : (
        <label className="font-weight-bold s-color">Description</label>
      )}
      <span className="s-color">
        <EditorToolbar toolbarId={fieldname} />
        <ReactQuill
          theme="snow"
          defaultValue={descriptionData}
          onChange={ondescription}
          placeholder={"Write something awesome..."}
          modules={modules(fieldname)}
          formats={formats}
        />
      </span>
    </div>
  );
}

export default EditorComponent;
